import wordsJSON from './ARRAY-json/1.2.json';
// console.log(wordsJSON);
// const initialState = {};
const initialState = {
	words: wordsJSON.words,
	currentIndex: 0,
};


function words(state = initialState, action) {
	if (action.type === 'NEXT_WORD') {
		if (state.currentIndex < state.words.length - 1) {
			return {...state, currentIndex: state.currentIndex + 1};
		}
	} else if (action.type === 'PREV_WORD') {
		if (state.currentIndex > 0) {
			return {...state, currentIndex: state.currentIndex - 1};
		}
	}
	console.log(action);
	return state;
}


export default words;


