import React, { Component } from 'react';
import { connect } from 'react-redux';
import './styles.css';

class Footer extends Component {
    render() {
    	console.log(this);
        return (
            <div className="footer">
            	<button onClick={this.props.prev} className="footer-button left-button">prev</button>
            	<button onClick={this.props.next} className="footer-button right-button">next</button>
            </div>
        );
    }
}

export default connect(
	(state) => ({}),
	(dispatch) => ({
		next() {
			console.log('NEXT');
			dispatch({type: 'NEXT_WORD'});
		},

		prev() {
			dispatch({type: 'PREV_WORD'});
		}
	}),
)(Footer);


