import React, { Component } from 'react';
import './styles.css';


class Card extends Component {
	state = {
		flipped: false,
	};

	// constructor(props) {
	// 	super(props);
	// }

	onClick = (e) => {
		this.setState({flipped: !this.state.flipped});
	}

	componentWillReceiveProps(nextProps) {
		console.log(nextProps);
		this.setState({
			flipped: false
		});
	}

	render() {
		return (
			<div onClick={this.onClick} className={`card ${this.state.flipped ? 'flipped' : ''}`}>
				<div className="front">
					<h1>
						{this.props.word}
					</h1>
				</div>
				<div className="back">
					<h1>
						{this.props.translate}
					</h1>
				</div>
			</div>);
	}
}

export default Card;


